* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body, html {
    overscroll-behavior: none;
    background-color: #000000;
    height: 100vh;
    width: 100%;
}

main {
    position: fixed;
    height: 100vh;
    width: 100%;
}

.scrollable {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    will-change: transform;
}

.image-container {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

h1 {
    position: absolute;
    left: 55%;
    top: 60%;
    z-index: 5;
    color: #ffffff;
}

img {
    position: absolute;
    height: 60%;
    visibility: hidden;
}

canvas {
    position: fixed;
    z-index: -1;
    top: 0;
    left: 0;
}